import React, { useEffect } from 'react';
import "../App.css";

function Privacy() {
  
  useEffect(() => {
    const htmlTitle = document.querySelector("title");
    htmlTitle.innerHTML = "Inside Privacy policy";
  }, [])

  return (
    <div className="Privacy">
        <div>privacy policy</div>
        <br/>
        <div>귀하 뉴토에 제공한 개인정보는 본 어플리케이션와 메타버스 서비스(이하 “앱”)에 의해 뉴토의 의해 처리됩니다. 본 앱에서 뉴토가 귀하의 개인정보에 대한 안전한 사용과 데이터 보호에 대해 알려드리고자 합니다.</div>
        <br/>
        <div>이정책에 설명된 데로 본 앱를 사용하거나 뉴토에 개인정보를 제출함으로써 귀하는 귀하의 개인정보를 뉴토가 사용하는 것을 동의합니다. 이 정책에 동의하지 않는 경우, 앱를 사용하거나 개인데이터를 뉴토에 제공하지 마시기 바랍니다.</div>
        <br/>
        <div>개인정보의 수집(Collection of Personal Data)</div>
        <div>일반적으로 귀하는 본 앱에 개인정보를 제공하고 하지 않고 방문할 수 있습니다. 뉴토에 제출된 개인정보(이름, 전화번호, 이메일주소, 주소, 팩스번호)등은 뉴토에 의해 수집될 수 있습니다. 등록, 가입을 완료하거나 우리의 제품과 서비스에 대한 특정정보를 요청 시 우리의 설문조사에 응답하지 않을 시 뉴토의 귀하의 개인 정보를 수집하지 않습니다.</div>
        <br/>
        <div>이용 목적(Purpose of Use)</div>
        <div>뉴토는 당사 제품의 사용을 지원하기 위해 귀하의 개인정보를 요청하여 사용할 수 있습니다. 뉴토는 고객의 요구와 더 나은 제품 개선을 위해 귀하의 개인정보와 설문 조사를 수집하여 사용 할 수 있습니다.</div>
        <br/>
        <div>개인정보의 전송 또는 공개(Transfer or Disclosure of Personal data)</div>
        <div>뉴토는 직접 마케팅 목적으로 제3자에게 개인정보를 제공하지 않습니다.</div>
        <br/>
        <div>뉴토는 자회사와 뉴토의 대리점에게 귀하가 요청한 서비스에 접속하기 위해 새 사용자로 등록할 것을 요청하며 이 자료를 전송할 수 있습니다. 이러한 데이터 전송은 해당 서비스 제공자에게 서비스 요청을 하기 위해 전송하는 것입니다. 예를 들어 AS등의 고객 서비스등을 위해 제공됩니다.</div>
        <br/>
        <div>뉴토는 수색영장 또는 다른 법적으로 유효한 요청 또는 명령에 따라 개인정보를 제공할 수 있습니다.</div>
        <br/>
        <div>비 개인 데이터(Non-Personal Data)</div>
        <div>귀하가 앱에 접속할 때 서비스 개선을 위해 귀하의 인터넷 브라우저, 운영 시스템, 접속 도메인 이름, 방문횟수, 평균 방문 시간을 수집하여 데이터화 합니다. 이 데이터를 모니터링하고 성능과 내용의 개선을 위해 자회사와 공유할 수 있습니다.</div>
        <br/>
        <div>보안(Security)</div>
        <div>뉴토는 뉴토 웹앱에서 수집 된 모든 개인정보와 기밀 유지를 위해 당사 직원과 정책에 설명 된 정보를 필요로 하는 서비스 제공업체에만 공개할 것 입니다.</div>
        <br/>
        <div>귀하의 개인정보에 대한 접속과 수정(Access to Your Personal Data and Correction of Data)</div>
        <div>뉴토는 귀하의 개인 정보를 검토하고 해결하기 위해 부정확한 내용을 수정하거나 사용자의 개인정보에 대해 삭제를 원할 경우 응답할 것 입니다. 삭제나 수정을 원할 경우 아래 주소로 전자 메일을 보내주시기 바랍니다.</div>
        <br/>
        <div>본 정책의 변경(Changes to This Policy)</div>
        <div>뉴토는 본 정책에 대해 언제든지 변경할 수 있으며 변경 사항은 앱에 즉각적으로 게시됩니다. 귀하가 정책의 개정에 동의하지 않는 경우 위의 연락처 주소로 전자메일 혹은 일반우편으로 연락주시기 바랍니다. 본 앱를 계속 사용시 본 정책에 동의하는 것으로 간주합니다.</div>
    </div>
  );
}

export default Privacy;
